import { Observable, OperatorFunction } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

export function propertiesListToArray(): OperatorFunction<string, string[]> {
  return source$ =>
    source$.pipe(
      map(properties => properties.replace(/\s/g, '').split(',')),
      map(properties => properties.filter(Boolean)),
    );
}

export const shareReplayOnce = () =>
  function <T>(source: Observable<T>) {
    // use refCount: true to prevent memory leaks issue with shareReplay
    return source.pipe(shareReplay({ refCount: true, bufferSize: 1 }));
  };
