import { Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { BehaviorSubject, defer, Observable, of } from 'rxjs';
import { first, publishReplay, refCount } from 'rxjs/operators';
import { getPropertyValue } from '../helpers/item-model-property';
import { propertiesListToArray } from '../helpers/rxjs-operators';
import { modelItemToArray } from '../helpers/type-helpers';

export interface BaseWidgetProperties {
  allowedRoutes: string[];
  origins: string[];
  severityLevels: string[];
}

export const DefaultBaseWidgetProperties: BaseWidgetProperties = {
  allowedRoutes: [],
  origins: [],
  severityLevels: [],
};

@Injectable()
export class BaseWidgetPropertiesService {
  constructor(@Optional() protected readonly itemModel?: ItemModel) {}

  private readonly allowedRoutes$$ = new BehaviorSubject<string[] | undefined>(undefined);
  private readonly origins$$ = new BehaviorSubject<string[] | undefined>(undefined);
  private readonly severityLevels$$ = new BehaviorSubject<string[] | undefined>(undefined);

  readonly allowedRoutes$ = getPropertyValue(this.allowedRoutes$$, this.allowedRoutes);
  readonly origins$ = getPropertyValue(this.origins$$, this.origins);
  readonly severityLevels$ = getPropertyValue(this.severityLevels$$, this.severityLevels);

  private get allowedRoutes(): Observable<string[]> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultBaseWidgetProperties.allowedRoutes);
    }

    return this.itemModel.property('allowedRoutes', '').pipe(first(), propertiesListToArray());
  }

  private get origins(): Observable<string[]> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultBaseWidgetProperties.origins);
    }

    return this.itemModel.property('origins', '').pipe(propertiesListToArray());
  }

  private get severityLevels(): Observable<string[]> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultBaseWidgetProperties.severityLevels);
    }

    return this.itemModel.property('severityLevels', '').pipe(propertiesListToArray());
  }

  setAllowedRoutes(value: string): void {
    this.allowedRoutes$$.next(modelItemToArray(value));
  }

  setOrigins(value: string): void {
    this.origins$$.next(modelItemToArray(value));
  }

  setSeverityLevels(value: string): void {
    this.severityLevels$$.next(modelItemToArray(value));
  }

  protected defaultModelObservable<T>(value: T): Observable<T> {
    return defer(() => of(value)).pipe(publishReplay(1), refCount());
  }
}
