import { Inject, LOCALE_ID, Pipe, PipeTransform } from '@angular/core';
import { NotificationItem } from '../models/notification-item';

export interface NotificationLocalization {
  [key: string]: {
    message: string;
    title: string;
  };
}

@Pipe({
  name: 'notificationLocalization',
})
export class NotificationLocalizationPipe implements PipeTransform {
  constructor(@Inject(LOCALE_ID) private readonly locale: string) {}

  transform(notification: NotificationItem, property: 'message' | 'title'): string {
    return (
      (((notification.translations || {}) as NotificationLocalization)[this.locale] || notification)[property] || ''
    );
  }
}
