import { ChangeDetectorRef, Directive, ElementRef, Input, AfterViewInit } from '@angular/core';
import { CheckTruncation } from '../models/check-truncation.model';
import { isElementContentTruncated } from './../helpers/is-truncated';

@Directive({
  selector: '[bbIsElementContentTruncated]',
})
export class IsElementContentTruncatedDirective implements AfterViewInit {
  constructor(private readonly elementRef: ElementRef, private readonly cd: ChangeDetectorRef) {}

  @Input() hostRef!: CheckTruncation;

  ngAfterViewInit() {
    setTimeout(() => {
      this.setIsContentTruncatedInParentComponent();
    });
  }

  private setIsContentTruncatedInParentComponent(): void {
    const isContentTruncated = isElementContentTruncated(this.elementRef.nativeElement);
    this.hostRef.setIsContentTruncated(isContentTruncated);
    this.cd.markForCheck();
  }
}
